.Menu {
  @apply fixed grid bottom-0 w-screen bg-blue-700;

  box-shadow: 0 -10px 15px -3px rgba(0, 0, 0, 0.1),
    0 -4px 6px -2px rgba(0, 0, 0, 0.05);
  grid-template-columns: repeat(auto-fit, minmax(0, 1fr));
}

@media screen and (min-width: 768px) {
  .Menu {
    @apply flex flex-col right-0 w-auto h-screen;

    box-shadow: -10px 0 15px -3px rgba(0, 0, 0, 0.1),
      -4px 0 6px -2px rgba(0, 0, 0, 0.05);
  }

  .MenuItem {
    @apply flex-row flex-no-wrap;
  }
}

.MenuItem {
  @apply flex p-1 flex-col content-center items-center text-gray-500 text-2xl outline-none;
}

.MenuItem.Active,
.MenuItem:hover,
.MenuItem:focus {
  color: white;
}

.MenuItem p {
  font-size: 0.5em;
  margin: 0;
}
